/* Week6.css */

/* Container styles */
.week6-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color:aliceblue; /* Light blue background */
  }
  
  /* Task container styles */
  .task-container {
    margin: 20px;
    padding: 20px;
    border: 20px solid ; /* Black border */
    border-radius: 30px;
  }
  
  /* Button styles */
  .toggle-button,
  .user-type-button {
    background-color: fuchsia; /* Dark turquoise */
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-right: 10px;
    transition: background-color 0.3s ease;
  }
  
  .toggle-button:hover,
  .user-type-button:hover {
    background-color: black; /* Light green on hover */
  }
  
  /* Component styles */
  .component {
    margin: 20px;
    padding: 20px;
    background-color: darkmagenta; /* Dark megenta background */
    border-radius: 10px;
    box-shadow: 0 4px 8px lavender(28, 193, 80, 0.973);
  }
  
  /* Colored line */
  .colored-line {
    border: 1px solid #1e1485; /* Dark blue line */
    width: 100%;
    margin: 40px auto;
  }
  
  /* Student details container */
  .student-details-container {
    display: flex;
    flex-wrap: wrap;
    justify-content:right;
    margin-top: 40px;
  }
  
  /* Student details card */
  .student-details {
    margin: 20px;
    padding: 20px;
    border: 5px solid mediumvioletred; /* Red border */
    border-radius: 10px;
    background-color: rosybrown; /* Light yellow background */
    box-shadow: 0 4px 8px red(63, 4, 4);
    width: 220px;
  }
  
  /* Subjects list styles */
  .subjects-list ul {
    padding-left: 20px;
  }

